import './App.css';
import { useEffect, useRef, useState } from 'react';
import { detection } from './faceShapeProcessor';
import { FilesetResolver, FaceLandmarker } from '@mediapipe/tasks-vision';
import Webcam from 'react-webcam';
import { base64_to_img } from './utils/commonFunctions';

function App() {


  const PATH_MODEL = 'https://storage.googleapis.com/mediapipe-models/face_landmarker/face_landmarker/float16/1/face_landmarker.task'
  const runningMode = "IMAGE"


  const textRefLength = useRef(null)
  const imgRef = useRef(null)
  const buttonRef = useRef(null)
  const canvasRef = useRef(null)
  const textRefJawline = useRef(null)
  const textRefResult = useRef(null)
  const webcamRef = useRef(null)

  const [progress, setProgress] = useState(0)
  const [webcamOpen, setWebcamOpen] = useState(false)
  const [disabled, setDisabled] = useState(true)
  const [camera, setCamera] = useState('environment')
  const [loaded, setLoaded] = useState(false)
  const [imgBase64, setImgBase64] = useState(null);
  const [imgSrc, setImgSrc] = useState(null);
  const [model, setModel] = useState(null);
  const [result, setResult] = useState("Detecting");

  const updateImgBase64 = (data) => {
    setImgBase64(data)
  }
  const updateImgSrc = (data) => {
    setImgSrc(data)
  }
  const updateResult = (data) => {
    setResult(data)
  }

  useEffect(() => {

    if (imgSrc) {
      // predictFaceShape()
    }
    else {
      const ctx = canvasRef.current.getContext("2d");
      ctx.clearRect(0, 0, 500, 500);
      textRefLength.current.innerText = 'Loading...'
      textRefJawline.current.innerText = ''
      textRefResult.current.innerText = ''
      readyApp()
    }

  }, [imgSrc])

  const readyApp = async () => {
    const face_landmarker = await loadModel();
    setLoaded(true)
    setModel(face_landmarker)
    setDisabled(false)
    document.getElementById('loading').innerText = 'Open Camera'

  }

  const loadModel = async () => {
    const vision = await FilesetResolver.forVisionTasks(
      // path/to/wasm/root
      "https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@latest/wasm"
    );
    const model = await FaceLandmarker.createFromOptions(
      vision,
      {
        baseOptions: {
          modelAssetPath: PATH_MODEL
        },
        runningMode: runningMode
      });

    if (model) {
      textRefLength.current.innerText = 'Successful!'
    }
    return model;

  }




  const predictFaceShape = async () => {

    detection(model, imgRef, canvasRef, textRefJawline, textRefResult, textRefLength)
    
  }

  function openCameraHandler() {

    if (!webcamOpen) {

      document.getElementById('webcam').classList.remove('d-none')
      document.getElementById('loading').innerText = 'Capture'
      document.getElementById('switch').classList.remove('d-none')
      const videoElement = webcamRef.current.video;
      videoElement.addEventListener('loadeddata', handleUserMedia);
      setWebcamOpen(true)
    }
    else if (webcamOpen) {
      const screenshot = webcamRef.current.getScreenshot()
      updateImgBase64(screenshot)
      const imageElement = base64_to_img(screenshot)
      updateImgSrc(imageElement.src)
      webcamRef.current.video.pause();
      webcamRef.current.video.srcObject = null;
      document.getElementById('webcam').classList.add('d-none')
      document.getElementById('switch').classList.add('d-none')
      setDisabled(true)
      document.getElementById('image').classList.remove('d-none')
      canvasRef.current.classList.remove('d-none')
      canvasRef.current.width = imgRef.current.offsetWidth;
      canvasRef.current.height = imgRef.current.offsetHeight
    }

  }

  const handleUserMedia = () => {

    document.getElementById('loading').innerText = 'Capture'
    setDisabled(false)
    document.getElementById('switch').classList.remove('d-none')

  }

  const cameraViewHandler = () => {

    document.getElementById('loading').innerText = 'Loading Camera'
    document.getElementById('switch').classList.add('d-none')
    setDisabled(true)


    if (camera === 'environment') {
      setCamera('user')
    }

    else {
      setCamera('environment')
    }

  }



  return (
    <>
      <div className="container my-5">

        <center>
          <h1>Faceshape Detection</h1>
          <div >
            <div
              className='button r d-none'
              id="switch">
              <input onClick={cameraViewHandler} type="checkbox" className="checkbox" />
              <div className="knobs"></div>
              <div className="layer"></div>
            </div>
          </div>
          <button
            disabled={disabled}
            id='loading'
            className="btn btn-primary fs-3 my-3"
            onClick={openCameraHandler}
          >
            Loading
          </button>

          <div>
            <Webcam
              id="webcam"
              ref={webcamRef}
              screenshotFormat="image/jpeg"
              className='d-none'
              mirrored={camera === 'user'} style={
                {

                  // maxWidth: '550px',
                  // width: '100%',
                  width: '300px',
                  height: '300px',
                  // objectFit: 'fill'
                  // height: `${document.getElementById('webcam').offsetWidth}`,
                  // height: '100%',
                  // maxHeight: '550px'

                }

              }
              videoConstraints={{
                facingMode: camera
              }}
            />
          </div>



          <div id='image' className="d-none ">
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <div style={{ position: 'relative' }}>
                <div>

                  <img onLoad={predictFaceShape} ref={imgRef} src={imgSrc}  alt="" />

                  <canvas
                    ref={canvasRef}
                    id="output-canvas"
                    className='d-none'
                    style={{
                      position: 'absolute',
                      zIndex: '90',
                      left: 0,
                      width: '300px',
                      height: '300px',
                    }}
                  ></canvas>

                </div>
              </div>
            </div>

          </div>

          <div >
            <div className='container bg-primary text-white mt-3  py-2'>
              <span ref={textRefLength} id="test"></span>

            </div>
            <div className='container bg-danger text-white mt-3  py-2'>

              <span ref={textRefJawline} ></span>

            </div>
            <div className='container bg-success text-white mt-3  py-2'>

              <span ref={textRefResult}></span>
            </div>
          </div>


        </center>
      </div>
    </>
  );
}

export default App;
