import { FilesetResolver, FaceLandmarker, DrawingUtils } from '@mediapipe/tasks-vision';


function distanceVector(v1, v2) {
    var dx = v1.x - v2.x;
    var dy = v1.y - v2.y;

    return Math.sqrt(dx * dx + dy * dy);
}



export const detection = async (faceLandmarker, imgRef, canvasRef, textRefJawline, textRefResult, textRefLength) => {
    //await loadModel(textRefLength)
    const width=imgRef.current.offsetWidth
    const height=imgRef.current.offsetHeight

    const faceLandmarkerResult = faceLandmarker.detect(imgRef.current);

    // console.log("top")
    // console.log(faceLandmarkerResult.faceLandmarks[0][10])
    // console.log("bottom")
    // console.log(faceLandmarkerResult.faceLandmarks[0][152])
    // console.log("left")

    // console.log(faceLandmarkerResult.faceLandmarks[0][366])
    // console.log("right")
    // console.log(faceLandmarkerResult.faceLandmarks[0][137])

  


    // Create an off-screen canvas
    const offscreenCanvas = document.createElement('canvas');
    offscreenCanvas.width = width;
    offscreenCanvas.height = height;
    const offscreenContext = offscreenCanvas.getContext('2d');

    // Draw the input image on the off-screen canvas
    offscreenContext.drawImage(imgRef.current, 0, 0, width, height);

    // console.log("offsceenOntext created!")

    // Get the color at the target point

    // console.log("top")
    // console.log(faceLandmarkerResult.faceLandmarks[0][10].x * 500)
    // console.log(faceLandmarkerResult.faceLandmarks[0][10].y * 500)
    // console.log(faceLandmarkerResult.faceLandmarks)
    const pixelData = offscreenContext.getImageData(width * faceLandmarkerResult.faceLandmarks[0][10].x, width * faceLandmarkerResult.faceLandmarks[0][10].y, 1, 1).data;

    // console.log(pixelData)

    const color = {
        red: pixelData[0],
        green: pixelData[1],
        blue: pixelData[2],
    };
    // console.log(color);

    let forhead = false;

    let forhead_X = width * faceLandmarkerResult.faceLandmarks[0][10].x
    let forhead_Y = height * faceLandmarkerResult.faceLandmarks[0][10].y


    while (!forhead) {
        forhead_Y--
        const pixelData = offscreenContext.getImageData(forhead_X, forhead_Y, 1, 1).data;
        const loop_color = {
            red: pixelData[0],
            green: pixelData[1],
            blue: pixelData[2],
        };
        let red_difference = Math.abs(loop_color.red - color.red)
        let blue_difference = Math.abs(loop_color.blue - color.blue)
        let green_difference = Math.abs(loop_color.green - color.green)


        const limit = 18
        if (red_difference > limit || blue_difference > limit || green_difference > limit) {
            // console.log("after color detection")
            // console.log(forhead_X, forhead_Y)
            forhead = true;
        }
    }

    const newTop = {
        x: forhead_X / width,
        y: forhead_Y / height
    }
    faceLandmarkerResult.faceLandmarks[0][10] = { ...newTop }


    //for text  length

    let length = distanceVector(faceLandmarkerResult.faceLandmarks[0][10], faceLandmarkerResult.faceLandmarks[0][152])

    const width_face = distanceVector(faceLandmarkerResult.faceLandmarks[0][234], faceLandmarkerResult.faceLandmarks[0][454])
    // const width=distanceVector(faceLandmarkerResult.faceLandmarks[0][366],faceLandmarkerResult.faceLandmarks[0][137])


    textRefLength.current.innerText = `Distance between top and bottom in xy-axis: ${length} \n
    Distance between left  and right in xy-axis: ${width_face} \n

    Difference in result : length(xy-axis)-width(xy-axis)= ${length - width_face} \n
    `
    const result = length - width_face > 0.16 ? 'long' : length - width_face > 0.1 ? 'medium' : 'small'

    textRefLength.current.innerText += `result: ${result}\n`

    //for text jawline
    const x_chin = Math.abs(faceLandmarkerResult.faceLandmarks[0][152].x - faceLandmarkerResult.faceLandmarks[0][400].x)
    const y_chin = Math.abs(faceLandmarkerResult.faceLandmarks[0][152].y - faceLandmarkerResult.faceLandmarks[0][400].y)
    const angle_chin = Math.atan(y_chin / x_chin)
    const degree_angle_chin = angle_chin * 180 / Math.PI
    const answer_chin = degree_angle_chin < 10 ? 'rounded' : 'sharp'

    const x_jaw = Math.abs(faceLandmarkerResult.faceLandmarks[0][397].x - faceLandmarkerResult.faceLandmarks[0][400].x)
    const y_jaw = Math.abs(faceLandmarkerResult.faceLandmarks[0][397].y - faceLandmarkerResult.faceLandmarks[0][400].y)
    const angle_jaw = Math.atan(y_jaw / x_jaw)
    const degree_angle_jaw = angle_jaw * 180 / Math.PI
    const answer_jaw = degree_angle_jaw < 50 && degree_angle_jaw > 40 ? 'angled' : 'rounded'


    const x_cheek = Math.abs(faceLandmarkerResult.faceLandmarks[0][397].x - faceLandmarkerResult.faceLandmarks[0][251].x)
    const y_cheek = Math.abs(faceLandmarkerResult.faceLandmarks[0][397].y - faceLandmarkerResult.faceLandmarks[0][251].y)
    const angle_cheek = Math.atan(y_cheek / x_cheek)
    const degree_angle_cheek = angle_cheek * 180 / Math.PI;


    textRefJawline.current.innerText = `\n Angle between green and yellow is ${degree_angle_chin}`
    textRefJawline.current.innerText += `\n Angle between yellow and red is ${degree_angle_jaw}`
    textRefJawline.current.innerText += `\n Angle between red and aqua is ${degree_angle_cheek}`
    textRefJawline.current.innerText += `\n chin Could be ${answer_chin} \n jawline could be ${answer_jaw}`




    //for drawing on screen
    canvasRef.current.width = canvasRef.current.offsetWidth;
    canvasRef.current.height = canvasRef.current.offsetHeight
    const ctx = canvasRef.current.getContext("2d");
    ctx.width =
        ctx.clearRect(0, 0, canvasRef.current.height, canvasRef.current.width);

    const point_to_test = faceLandmarkerResult.faceLandmarks[0][152]
    const point_to_test2 = faceLandmarkerResult.faceLandmarks[0][397]
    const point_to_test3 = faceLandmarkerResult.faceLandmarks[0][378]
    const point_to_test4 = faceLandmarkerResult.faceLandmarks[0][400]
    const point_to_test5 = faceLandmarkerResult.faceLandmarks[0][251]
    const point_to_test6 = faceLandmarkerResult.faceLandmarks[0][10]
    const point_to_test7 = faceLandmarkerResult.faceLandmarks[0][234]
    const point_to_test8 = faceLandmarkerResult.faceLandmarks[0][454]

    ctx.beginPath();

    ctx.fillStyle = 'white';
    ctx.fillRect(point_to_test7.x * width - 2.5, point_to_test7.y * height - 2.5, 5, 5);
    ctx.fillStyle = 'white';
    ctx.fillRect(point_to_test8.x * width - 2.5, point_to_test8.y * height - 2.5, 5, 5);

    ctx.fillStyle = 'lightGreen';
    ctx.fillRect(point_to_test.x * width - 2.5, point_to_test.y * height - 2.5, 5, 5);
    ctx.fillStyle = 'lightGreen';
    ctx.fillRect(point_to_test6.x * width - 2.5, point_to_test6.y * height - 2.5, 5, 5);
    ctx.fillStyle = 'yellow';
    ctx.fillRect(point_to_test4.x * width - 2.5, point_to_test4.y * height - 2.5, 5, 5);
    ctx.fillStyle = 'blue';
    ctx.fillRect(point_to_test3.x * width - 2.5, point_to_test3.y * height - 2.5, 5, 5);
    ctx.fillStyle = 'red';
    ctx.fillRect(point_to_test2.x * width - 2.5, point_to_test2.y * height - 2.5, 5, 5);
    ctx.fillStyle = 'aqua';
    ctx.fillRect(point_to_test5.x * width - 2.5, point_to_test5.y * height - 2.5, 5, 5);
    ctx.fill()

    // console.log(faceLandmarkerResult.faceLandmarks[0][10].x, faceLandmarkerResult.faceLandmarks[0][10].y)
    // console.log(point_to_test.x*500, point_to_test.y*500)
    // console.log(FaceLandmarker.FACE_LANDMARKS_FACE_OVAL)


    //displaying result for face shape
    var answer;
    console.log(result)
    switch (result) {
        case 'long':
            answer = 'oblong'
            break;
        case 'medium':
            if (answer_jaw === 'angled') {
                if (answer_chin === 'rounded') {
                    answer = 'diamond'
                }
                else if (answer_chin === 'sharp') {
                    answer = 'triangle'
                }
            }
            else if (answer_jaw === 'rounded') {
                if (answer_chin === 'rounded') {
                    answer = 'oval'
                }
                else if (answer_chin === 'sharp') {
                    answer = 'heart'
                }
            }
            break;
        case 'small':

            if (answer_jaw === 'angled') {
                answer = 'square'
            }
            else if (answer_jaw === 'rounded') {
                answer = 'round'
            }
            break;

        default:
            break;
    }
    textRefResult.current.innerText = `\n Your estimated Face shape is ${answer}`




    const drawingUtils = new DrawingUtils(ctx);
    for (const landmarks of faceLandmarkerResult.faceLandmarks) {

        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_TESSELATION,
        //   { color: "red", lineWidth: 1 }
        // );
        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_RIGHT_EYE,
        //   { color: "#FF3030" }
        // );
        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_RIGHT_EYEBROW,
        //   { color: "#FF3030" }
        // );
        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_LEFT_EYE,
        //   { color: "#30FF30" }
        // );
        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_LEFT_EYEBROW,
        //   { color: "#30FF30" }
        // );
        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_FACE_OVAL,
        //   { color: "red" }
        // );
        // drawingUtils.drawConnectors(landmarks, FaceLandmarker.FACE_LANDMARKS_LIPS, {
        //   color: "#E0E0E0"
        // });
        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_RIGHT_IRIS,
        //   { color: "#FF3030" }
        // );
        // drawingUtils.drawConnectors(
        //   landmarks,
        //   FaceLandmarker.FACE_LANDMARKS_LEFT_IRIS,
        //   { color: "#30FF30" }
        // );
    }



}